import React, { useEffect } from 'react'
import { useTransition, animated, config } from 'react-spring'
import MainContent from "../MainContent/MainContent"
import ContactCard from "../ContactCard/ContactCard"
import { CONTACTS, MAIN_TITLE } from "../../utils/utils"
import YandexMapsWidget from "../YandexMapsWidget/YandexMapsWidget";

function ContactsPage({ title }) {

  const transitions = useTransition(CONTACTS, {
    from: {
      opacity: 0,
      transform: `translate3d(30px,0,0)`
    },
    enter: {
      opacity: 1,
      transform: `translate3d(0,0,0)`
    },
    leave: {
      opacity: 0,
      transform: `translate3d(30px,0,0)`
    },
    trail: 200,
    delay: 100,
    config: config.stiff
  })

  useEffect(() => {
    document.title = `Контакты ${MAIN_TITLE}`
  }, [])

  return (
    <MainContent title={title}>

      {
        transitions((props, { icon, text }, key) =>
          <animated.div
            key={key.id} style={props}>
            <ContactCard key={key} icon={icon} text={text} />
          </animated.div>
        )
      }

      <h3 className="col s12 white-text">Как добраться?</h3>
      <YandexMapsWidget />
    </MainContent>
  )
}

export default ContactsPage
