import React, { useEffect } from 'react'
import { useSpring, animated, config } from 'react-spring'
import MainContent from "../MainContent/MainContent"
import './HomePage.sass'
import { MAIN_TITLE } from "../../utils/utils"

function HomePage(props) {
  const titleStyles = useSpring({
    from: {
      opacity: 0,
      position: 'relative',
      left: '-200px'
    },
    to: {
      opacity: 1,
      left: '0'
    },
    delay: 200,
    config: config.molasses
  })

  const subtitleStyles = useSpring({
    from: {
      opacity: 0,
      position: 'relative',
      top: '100px'
    },
    to: {
      opacity: 1,
      top: '0'
    },
    delay: 500,
    config: config.molasses
  })

  useEffect(() => {
    document.title = `Главная ${MAIN_TITLE}`
  }, [])

  return (
    <MainContent>
      {
        <figure className="home-figure quote white-text">
          <animated.div style={titleStyles}>
            <h1 className="home-figure__title">
              Лучше иметь и не нуждаться,
              чем нуждаться и не иметь.
            </h1>
          </animated.div>
          <animated.div style={subtitleStyles} >
            <h4 className="home-figure__subtitle">— Чарльз Мартин.</h4>
          </animated.div>
        </figure>
      }
    </MainContent>
  )
}

export default HomePage
