import React, { useState, useEffect } from 'react'
import M from 'materialize-css'

function BurgerMenuButton(props) {
  const [isSidenavOpen, setisSidenavOpen] = useState(false)

  useEffect(() => {
    const sidenav = document.querySelector('.sidenav')
    M.Sidenav.init(sidenav, {
      edge: 'left'
    })
    
    const instance = M.Sidenav.getInstance(sidenav)

    return isSidenavOpen ? instance.open() : instance.close()
  }, [isSidenavOpen])

  const handleBurgerClick = () => setisSidenavOpen(prev => !prev)

  return (
    <i onClick={handleBurgerClick} className="material-icons hide-on-med-and-up">menu</i>
  )
}

export default BurgerMenuButton
